.material-icons.text-4xl{
  font-size: 2rem;
}
.recommendation-wall::-webkit-scrollbar {
  display: none;
  scrollbar-width: none;
  -ms-overflow-style: none;
}
.recommendation-card{
  min-width: 350px;
}
.recommendation-card:not(:last-child){
  margin-right: 30px;
}


// js 樣式
header{
  a[data-scroll="true"]{
    @apply text-primary;
  }
}
[data-topMenu-burger="show"]{
  @apply text-primary;
}
[data-topMenu-burger="close"]{
  @apply text-dark;
}
[data-loading="true"]{
  @apply flex;
}
[data-loading="false"]{
  @apply hidden;
}
[data-cartMsg="true"]{
  @apply table;
}
[data-cartMsg="false"]{
  @apply hidden;
}
[data-popUp="true"]{
  @apply flex;
}
[data-popUp="false"]{
  @apply hidden;
}